/* Основная секция цен */
.priceSection {
    width: 1400px;
    margin: auto;
    margin-top: 100px;
}


.title {
    font-size: 36px;
    color: #263238;
    margin: 0px;
    text-align: center;
    margin-bottom: 50px;
}

/* Список ценовых блоков */
.priceSection ul {
    margin-top: 100px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    gap: 20px; /* Добавлен небольшой отступ между элементами */
    padding: 0;
}

/* Каждый ценовой блок */
.priceSection li {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
}

/* Изображения */
.priceImg1 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

.priceImg2 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

.priceImg3 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

/* Заголовки */


.priceSection h4 {
    margin: 20px 0;
    text-align: start;
    width: 375px;
    font-size: 30px;
    line-height: 1.6;
    color: #263238;
}

/* Описание */
.priceSection p {
    font-family: 'Poppins', "Roboto";
    text-align: start;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.5;
    margin: 0 0 20px 0;
}

.priceDesc {
    width: 370px;
    color: #263238;
}

/* Блок с ценой */
.priceCost {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 240px;
    padding: 10px 0;
    color: white;
    background-color: #1b6bd4;
    border-radius: 5px;
    font-family: 'Poppins', "Roboto";
}

.value {
    display: flex;
    align-items: center;
    font-size: 30px;
    line-height: 1.5;
    font-family: 'Poppins', "Roboto";
}

.priceSection > ul li:nth-child(1)  h4{
    margin-top: 35px;
} 

/* ====== Медиа-запросы для адаптива ====== */

/* Для больших экранов (1400px и больше) */
@media (min-width: 1400px) {
    .priceSection {
        width: 1400px;
    }
    .priceSection ul {
        padding-left: 30px;
        padding-right: 30px;
    }
    .priceSection p {
        font-size: 20px;
        font-weight: 400;
    }
    .value {
        font-size: 24px;
    }
    .manth {
        font-size: 22px;
    }
    
}







@media (min-width: 1231px) and (max-width: 1399px) {
    .priceSection {
        width: 90%;
        margin-top: 70px;
    }

    .title {
        margin-bottom: 10px;
    }

    .priceSection ul {
        gap: 15px;
        justify-content: space-between;
        margin-top: 80px;
        padding-left: 30px;
        padding-right: 30px;
    }

    .priceImg {
        width: 280px;
        height: 280px;
    }

    .priceSection h4 {
        width: 300px;
        font-size: 26px;
    }

    .priceDesc {
        width: 320px;
       
    }

    .priceCost {
        width: 200px;
    }

    .value {
        font-size: 20px;
    }

    .manth {
        font-size: 16px;
    }

}


/* Desktop (1024px - 1231px) */
@media (min-width: 1024px) and (max-width: 1231px) {
    .priceSection {
            width: 90%;
            margin-top: 50px;
    }

    .priceImg1 {
        width: 260px;
        height: 260px;
    }
    .priceImg2 {
        width: 260px;
        height: 260px;
    }
    .priceImg3 {
        width: 260px;
        height: 260px;
    }

    .priceSection ul {
        margin-top: 70px;
        padding: 30px;
        padding-top: 0px;
    }

    .priceSection li {
        width: 290px;
    }

    .priceSection > ul li:nth-child(1)  h4{
        margin-top: 28px;
    } 

    .priceSection > ul li:nth-child(2) .priceCost{
        margin-top: 20px;
    } 

    .priceSection > ul li:nth-child(3) .priceCost{
        margin-top: 20px;
    } 

    .priceDesc {
        width: 280px;
    }

    .priceSection h4 {
        width: 280px;
        font-size: 24px;
    }

    .priceSection p {
        font-size: 22px;  
        font-weight: 400;
    }


    .priceCost {
        width: 210px;
        margin-right: 100px;
    }

    .value {
        font-size: 22px;
    }

    .manth {
        font-size: 16px;
    }




}


/* Для планшетов (768px - 1023px) */
@media (max-width: 1023px) {
    .priceSection {
        width: 90%;
        margin-top: 70px;
    }

    .priceSection ul {
        display: flex;
        flex-direction: column;    /* Перенос элементов на новую строку */
        justify-content: space-around;
        align-items: center;
        gap: 30px;
        margin-top: 20px;
    }

    .priceSection li {
        align-items: center;
    }

    .title {
        font-size: 38px;
        color: #263238;
        margin: 0px;
        text-align: center;
        margin-bottom: 50px;
    }

    .priceImg1 {
        width: 200px;
        height: 200px;
    }

    
    .priceImg2 {
        margin-top: 10px;
         width: 210px;
        height: 210px;
    }

    
    .priceImg3 {
        margin-top: 40px;
        width: 219px;
        height: 180px;
    }


    .priceSection h4 {
        font-size: 28px;
        margin: 0;
        text-align: center;
        margin-bottom: 20px;
    }

    .priceSection > ul li:nth-child(1)  h4{
        margin-top: 20px;
    } 

    .priceSection > ul li:nth-child(2) .priceCost{
        margin-top: 20px;
    } 

    .priceSection > ul li:nth-child(3) .priceCost{
        margin-top: 20px;
    } 


    .priceSection p {
        font-size: 18px;
        font-weight: 500;
        text-align: center;
    }


    .priceCost {
        width: 140px;
        text-align: start;
    }

    .value {
        font-size: 18px;
    }

    .manth {
        font-size: 14px;
    }
    .priceDesc {
        width: 90%;
    }
}

/* Для мобильных устройств (до 767px) */
@media (max-width: 767px) {
    .priceSection {
        width: 90%;
        margin: 50px auto;
    }

   .title{
    font-size: 35px;
    margin-bottom: 0px;
   }

    .priceSection ul {
        flex-direction: column;
        gap: 40px;
        align-items: center;
        margin-top: 40px;
    }

    .priceSection li {
        width: 100%;
        max-width: 350px;
        align-items: center;
    }

    .priceImg1 {
        width: 248px;
        height: 248px;
        padding-left: 20px;
    }

    
    .priceImg2 {
        width: 241px;
        height: 241px;
        padding-left: 20px;
    }

    
    .priceImg3 {
        width: 257px;
        height: 197px;
        padding-left: 20px;
    }

    .title{
        color: #4e4e4e;
        font-size: 30px;
        font-weight: 700;
        text-align: center;
        margin: 0px;
        }

    .priceSection h4 {
        font-size: 21px;
        width: 100%;
        text-align: center;
    }

    .priceDesc {
        font-size: 15px;
        width: 100%;
        text-align: center;
        padding: 0 10px;
    }

    .priceSection p {
        font-size: 17px;
        text-align: center;
    }

    .priceCost {
        width: 170px;
        padding: 8px 0;
    }

    .value {
        font-size: 21px;
    }

    .manth {
        font-size: 14px;
    }
}
