/* === Основные стили === */
.container {
  width: 1400px;
  max-width: 1400px;
  text-align: center;
  margin: auto;
  padding: 10px;
}

.title {
  font-family: 'Poppins', "Roboto";
  color: #263238;
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 30px;
}

.skillsGrid {
  display: flex;
  justify-content: center;
  flex-direction: row;
  gap: 20px;
  margin: auto;
}

/* === Карточки === */
.card {
  background: #e0e0e0;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cardContent {
  text-align: start;
}

.cardContent h3,
.lastCardDesc h3 {
  font-family: 'Poppins', "Roboto";
    color: #263238;
  font-size: 30px;
  margin: 0;
}

.cardContent p,
.lastCardDesc p {
  font-family: 'Poppins', "Roboto";
  font-weight: 600;
    font-size: 19px;
  color: #263238;
  margin: 0 0 10px 0;
}

.skillsGrid > div:nth-child(1) {
  height: 840px;
  width: 415px;
}

.skillsGrid > div:nth-child(3) {
  padding: 0;
}

.skillsGrid > div:first-child .cardContent {
  width: 310px;
  height: 478px;
}

.skillsGrid .card:nth-child(1) .cardImage {
  width: 290px;
  height: 290px;
}

.secondBlock {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.secondBlock .card {
  width: 440px;
  height: 560px;
}

.secondBlock .card:nth-child(1) .cardImage {
  width: 236px;
  height: 236px;
}

.secondBlock .card:nth-child(2) .cardImage {
  width: 353px;
  height: 236px;
}

.secondBlock .card .cardContent p {
  width: 310px;
}

.cardLast {
  display: flex;
  background: #e0e0e0;
  border-radius: 10px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
  align-items: center;
  height: 257px;
}

.cardLast .cardImage {
  width: 300px;
  height: 200px;
  margin-left: 70px;
}

.cardLast p {
  width: 396px;
}

.lastCardDesc {
  padding: 20px 0 20px 60px;
}

.lastCardDesc button {
  margin-right: 100%;
}

/* === Группа из трёх карточек === */
.threeCard {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* === Кнопка === */
.button {
  font-family: 'Poppins', "Roboto";
  width: 200px;
  height: 50px;
  background: #1b6bd4;
  color: white;
  border: none;
  padding: 10px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s;
  margin-top: 15px;
}

.button:hover {
  background: #0056b3;
}








/* Стили для модалки */
.modalOverlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden; /* предотвращаем прокрутку фона */
}

.modalContent {
  background-color: white;
  padding: 40px 20px; /* Уменьшил боковые отступы для удобства */
  width: 800px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  overflow: hidden; /* Запрещаем прокрутку контейнера, но разрешаем внутри */
}

.modalInnerContent {
  overflow-y: auto;
}

/* Стили для изображения внутри модалки */
.modalImage {
  width: 100%;
  max-width: 300px;
  border-radius: 8px;
  margin-bottom: 15px;
}

/* Описание внутри модалки */
.modalContentDesc {
  margin-top: 0;
  background-color: rgb(230, 228, 228);
  padding: 30px 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  text-align: start;
  font-family: 'Poppins', 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: #263238;
}

/* Кнопка закрытия */
.modalCloseButton {
  font-family: 'Poppins', 'Roboto', sans-serif;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  padding: 10px 40px;
  background-color: #1b6bd4;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  align-self: flex-end; /* Выравниваем кнопку вправо */
}

.modalCloseButton:hover {
  background-color: #0c4897;
}








