/* Base Styles */
.headerContainer {
    width: 1400px;
    margin: auto;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    height: 60px;
}


.logo h2 {
    font-weight: 600;
    font-size: 35px;
    color: #8f8f8f;
    font-family: 'Poppins', 'Roboto';
}

.logo span {
    color: #1B5DB2;
    /* Основной акцентный цвет */
}

.nav {
    flex-grow: 1;
    text-align: center;
}

.navList {
    color: #263238;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

.navItem {
    font-family: 'Poppins', 'Roboto';
    margin: 0 15px;
    font-size: 22px;
    line-height: 1.5;
    cursor: pointer;
    transition: color 0.3s ease;
}

.navItem:hover {
    color: #A3AEB9;
    /* Цвет при наведении */
}

.ctaButton {
    width: 220px;
    height: 70px;
    font-size: 22px;
    font-weight: 500;
    border-radius: 10px;
    background-color: #dfdfdf;
    color: #818181;
    border: 2px solid #9b9b9b;
    font-family: "Poppins";
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.ctaButton:hover {
    background-color: #0056b3;
    /* Цвет при наведении */
}

.brg {
    display: none;
}

/* Media Queries */

/* Large Desktop (1440px и выше) */
@media (min-width: 1440px) {
    .headerContainer {
        width: 90%;
        margin-bottom: 50px;
    }

    /* Здесь можно увеличить отступы, размеры шрифтов и элементов */
    .brg {
        display: none;
    }


    .sideMenu {
        display: none;
    }



    .sideNavList {
        list-style: none;
        padding: 0;
        margin-bottom: 60px;
    }

    .sideNavItem {
        font-family: 'Poppins', 'Roboto';
        font-weight: 500;
        color: #263238;
        padding: 15px 0;
        border-bottom: 1px solid #eee;
        cursor: pointer;
        font-size: 18px;
        margin-bottom: 20px;
    }

    .socialLinks {
        padding: 0;
        width: 150px;
        margin-bottom: 50px;
        margin-left: 40px;
        display: flex;
        justify-content: space-between;
        list-style: none;
    }


    .socialImg {
        width: 30px;
    }



    .languageSelectorF {
        display: flex;
        margin-top: 18px;
        flex-direction: column;
        margin-bottom: 20px;
        /* Отступ снизу */
    }

    .languageLabel {
        position: absolute;
        background-color: white;
        z-index: 2;
        top: 15px;
        padding: 5px;
        padding-bottom: 0px;
        padding: 5px;
        font-size: 15px;
        font-weight: 500;
        margin-left: 10px;
    }

    .reactSelect {
        width: 200px;
        /* Обеспечит растягивание до максимальной ширины */
        font-size: 14px;
    }



}


