/* Tablet (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
    .headerContainer {
        width: 100%;
        position: fixed;
        background-color: white;
        border-bottom: 1px solid grey;
    }

    .logo h2 {
        font-size: 30px;
        margin: 0px;
    }

    .logo p {
        font-size: 30px;
    }

    .header {
        height: auto;
        /* Высота по содержимому */
        padding-top: 15px;
        padding-left: 25px;
        padding-right: 25px;
        padding-bottom: 15px;
    }

    .brg {
        display: block;
        width: 32px;
    }

    .navList {
        display: none;
    }


    .ctaButton {
        display: none;
    }





    .sideMenu {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        position: fixed;
        top: 0;
        right: -400px;
        /* Меню скрыто по умолчанию */
        width: 250px;
        height: 100vh;
        background-color: white;
        box-shadow: -2px 0 5px rgba(0, 0, 0, 0.3);
        transition: right 0.3s ease;
        z-index: 1000;
        padding: 20px;
    }

    .sideMenu.open {
        right: 0;
        /* Показываем меню при открытии */
    }

    .sideNavList {
        list-style: none;
        padding: 0;
    }

    .sideNavItem {
        padding: 15px 0;
        border-bottom: 1px solid #eee;
        cursor: pointer;
        font-size: 18px;
    }




    .sideNavList {
        list-style: none;
        padding: 0;
        margin-bottom: 60px;
    }

    .sideNavItem {
        font-family: 'Poppins', 'Roboto';
        font-weight: 500;
        color: #263238;
        padding: 15px 0;
        border-bottom: 1px solid #eee;
        cursor: pointer;
        font-size: 18px;
        margin-bottom: 20px;
    }

    .socialLinks {
        padding: 0;
        width: 150px;
        margin-bottom: 50px;
        margin-left: 40px;
        display: flex;
        justify-content: space-between;
        list-style: none;
    }


    .socialImg {
        width: 30px;
    }





    .languageSelectorF {
        display: none;
    }





    .languageSelector {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        /* Отступ снизу */
    }

    .languageLabel {
        text-align: start;
        font-size: 16px;
        color: #263238;
        margin-bottom: 10px;
        margin-left: 5px;
        font-weight: 600;
        text-align: left;
        /* Чтобы текст был выровнен по левому краю */
    }

    .reactSelect {
        width: 100%;
        /* Обеспечит растягивание до максимальной ширины */
        font-size: 14px;
    }





}






